@import "index.css";

.card__main {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 20px;
}

.card__container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background: #2b2b2b;
  color: white;
  padding: 30px;
  border-radius: 40px;
  width: 600px;
}

.card__header {
  display: flex;
  justify-content: flex-start;
  padding: 10px;
  width: 500px;
}

.card__nameOfAuthor-timeOfPost {
  display: flex;
  flex-direction: column;
}

.card__header-timeOfPost {
  color: gray;
  font-size: 13px;
  margin-top: 10px;
}

.card__header-autherImage {
  margin-right: 15px;
}

.card__content {
  padding: 10px;
  width: 450px;
  word-wrap: break-word;
}

.card__button-container {
  display: flex;
  width: 450px;
  height: 60px;
  justify-content: flex-start;
  align-items: center;
  margin-top: 20px;
}

.card__buttonLike {
  border: none;
  background: none;
  cursor: pointer;
}

.card__likeCounter {
  margin-left: 10px;
}

.card__postImage {
  width: 350px;
  height: 450px;
}

.card__header-autherImage {
  height: 35px;
  width: 35px;
  border-radius: 50%;
}
